import { useRef, useMemo, useEffect } from 'react';
import { extend, useLoader } from '@react-three/fiber';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';
extend({ TextGeometry });

const fontsrc = 'fonts/Jua_Regular.json';

const Typo3D = ({
  children,
  style = '',
  vAlign = 'center',
  hAlign = 'center',
  size = 1,
  color = '#000000',
  ...props
}) => {
  const font = useLoader(FontLoader, fontsrc);

  const config = useMemo(
    () => ({
      font,
      size: 3,
      height: 0.5, //ÏïûÎí§ ÎëêÍªòÍ∞ê
      curveSegments: 2, //ÎÑàÎ¨¥ ÎÜíÍ≤åÌïòÎ©¥ Î¶¨Î†åÎçîÎßÅ Ïò§ÎûòÍ±∏Î¶º
      bevelEnabled: true,
      bevelThickness: 1,
      bevelSize: 0.1,
      bevelOffset: 0.01,
      bevelSegments: 10,
    }),
    [font]
  );

  /** ÏÑºÌÑ∞ Ï†ïÎ†¨ */
  const mesh = useRef(null);
  useEffect(() => {
    if (mesh.current) {
      mesh.current.children[0].geometry.center();
    }
  }, [children]);

  return (
    <group ref={mesh} {...props} scale={[0.1 * size, 0.1 * size, 0.1]}>
      <mesh>
        <textGeometry attach="geometry" args={[children, config]} />
        <meshPhongMaterial shininess={200} color={color} attach="material" />
      </mesh>
    </group>
  );
};

export default Typo3D;
